/**
 * General
 */
html, body {
  margin: 0;
  color: black;
}

html .popup-open, body .popup-open {
  overflow: hidden;
}

.container {
  margin: 0 auto;
  width: 95%;
  max-width: 1140px;
}

#page {
  overflow: hidden;
  background-color: black;
}

.invisible {
  visibility: hidden;
}

/**
 * Typography
 */
body {
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 20px;
  line-height: normal;
  letter-spacing: 1px;
}

body *:not(.fa) {
  font-family: 'Roboto Condensed', sans-serif;
}

body.index p {
  font-size: 20px;
  line-height: 25px;
}

body.revise #page {
  background-color: transparent;
}

h1 {
  word-break: break-word;
  font-size: 64px;
  line-height: 60px;
  letter-spacing: 8px;
  font-weight: normal;
  margin: 20px 0;
}

h2 {
  font-weight: normal;
  font-size: 60px;
  line-height: 70px;
  letter-spacing: 7px;
  margin-bottom: 20px;
}

h2 div {
  font-size: 36px;
  line-height: 42px;
  letter-spacing: 4px;
}

a {
  letter-spacing: 1px;
}

a.learn-more {
  font-weight: bold;
}

.text-uppercase {
  text-transform: uppercase;
}

ul.no-bullet {
  list-style-type: none;
  padding-left: 0;
}

ul.no-spacing {
  margin: 0;
  padding: 0;
}

ul.full {
  width: 100%;
}

.button-desc {
  font-size: 20px;
  line-height: 40px;
  margin-top: 20px;
}

.register-message {
  line-height: 80px;
  letter-spacing: 1px;
}

/* Logo */
.branding-side .logo {
  position: relative;
}

.branding-side .logo > img {
  max-width: 100%;
}

.branding-side .logo .version {
  font-size: 15px;
}

.site-title {
  margin: 0;
  line-height: 10px;
}

.site-title a {
  color: black;
  text-decoration: none;
  font-weight: bold;
  font-size: 38px;
  line-height: 38px;
  letter-spacing: 4px;
  display: inline-block;
  position: relative;
}

.site-title a span {
  font-size: 15px;
}

@media (max-width: 785px) {
  .site-title a {
    font-size: 25px;
  }
}

@media (max-width: 595px) {
  .site-title a {
    font-size: 20px;
  }
}

/**
 * Elements
 */
img {
  max-width: 100%;
  height: auto;
}

input[type='range'] {
  appearance: none;
  width: 100%;
  height: 15px;
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#000000+0,000000+100&0+0,0.65+100 */
  background: linear-gradient(to right, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.65) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  outline: none;
  transition: opacity .2s;
}

input[type='range']::-webkit-slider-thumb {
  appearance: none;
  width: 25px;
  height: 25px;
  border-radius: 5px;
  background: black;
  cursor: pointer;
}

input[type='range']::-moz-range-thumb {
  width: 25px;
  height: 25px;
  border-radius: 5px;
  background: black;
  cursor: pointer;
}

input[type='range'] + .percentage {
  width: 45px;
  text-align: center;
  overflow: hidden;
}

input[type='range'] + .percentage::after {
  content: "%";
}

input[type='text'],
input[type='email'],
input[type='password'],
input[type='url'],
input[type='submit'],
button {
  display: block;
  padding: 0 10px;
  border: 2px solid black;
  border-radius: 8px;
  width: auto;
  max-width: 100%;
  box-sizing: border-box;
  height: 28px;
  font-size: 14px;
  background-color: white;
}

input[type='text'].large,
input[type='email'].large,
input[type='password'].large,
input[type='url'].large,
input[type='submit'].large,
button.large {
  font-size: 20px;
  height: 80px;
  border-width: 8px;
  padding: 0 20px;
  margin-bottom: -8px;
}

input[type='text'].full,
input[type='email'].full,
input[type='password'].full,
input[type='url'].full,
input[type='submit'].full,
button.full {
  width: 100%;
}

input[type='text'].no-padding,
input[type='email'].no-padding,
input[type='password'].no-padding,
input[type='url'].no-padding,
input[type='submit'].no-padding,
button.no-padding {
  padding: 0 2px;
}

input[type='text'].increaseable,
input[type='email'].increaseable,
input[type='password'].increaseable,
input[type='url'].increaseable,
input[type='submit'].increaseable,
button.increaseable {
  width: 55px;
  display: inline-block;
  text-align: center;
  padding: 0;
}

input[type='text'].invert,
input[type='email'].invert,
input[type='password'].invert,
input[type='url'].invert,
input[type='submit'].invert,
button.invert {
  color: white;
  background-color: black;
}

input[type='text'].invert > a,
input[type='email'].invert > a,
input[type='password'].invert > a,
input[type='url'].invert > a,
input[type='submit'].invert > a,
button.invert > a {
  color: white;
  background-color: black;
}

input[type='text'].invert:hover,
input[type='email'].invert:hover,
input[type='password'].invert:hover,
input[type='url'].invert:hover,
input[type='submit'].invert:hover,
button.invert:hover {
  background-color: white;
  color: black;
  border-color: black;
}

input[type='text'].invert:hover > a,
input[type='email'].invert:hover > a,
input[type='password'].invert:hover > a,
input[type='url'].invert:hover > a,
input[type='submit'].invert:hover > a,
button.invert:hover > a {
  background-color: white;
  color: black;
  border-color: black;
}

input[type='text']:focus,
input[type='email']:focus,
input[type='password']:focus,
input[type='url']:focus,
input[type='submit']:focus,
button:focus {
  outline: none;
  box-shadow: 0px 0px 5px 1px #007acc;
}

.button-oval {
  display: inline-block;
  border: 1px solid black;
  border-radius: 30px;
  padding: 5px 15px;
}

/* Button specific */
input[type='submit'],
button {
  display: inline-block;
  width: auto;
  max-width: 100%;
  text-transform: uppercase;
  font-size: 14px;
  letter-spacing: normal;
  cursor: pointer;
  background-repeat: no-repeat;
  background-position: 100% 100%;
  padding: 0 10px;
}

input[type='submit']:hover,
button:hover {
  color: #007acc;
}

input[type='submit'].small,
button.small {
  border-width: 2px;
  border-radius: 4px;
  font-size: 20px;
  line-height: 35px;
  height: auto;
  background: none;
  padding: 0 25px;
  min-width: 130px;
}

input[type='submit'].large,
button.large {
  font-size: 25px;
  letter-spacing: 3px;
  height: 80px;
  border-width: 8px;
  padding: 0 40px;
  margin-bottom: -8px;
  background-image: url("/assets/icons/triangle.svg");
}

input[type='submit'].dark,
button.dark {
  background-color: black;
  color: white;
}

input[type='submit'].dark:hover,
button.dark:hover {
  background-color: #007acc;
}

textarea {
  display: block;
  width: 100%;
  height: 90px;
  border: 3px solid black;
  box-sizing: border-box;
  resize: none;
  padding: 2px 5px;
  font-size: 14px;
}

.filter-blocks {
  position: relative;
}

.filter-blocks i {
  cursor: pointer;
}

.filter-blocks input[type="text"] {
  display: inline-block;
  width: auto;
  max-width: none;
  height: auto;
  border: 2px solid;
  padding: 2px 5px;
  font-size: 14px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  visibility: hidden;
  opacity: 0;
  right: -100%;
  transition: 500ms;
  border-radius: 8px;
  outline: none;
}

.filter-blocks input[type="text"]:focus {
  border-color: #007acc;
}

.filter-blocks input[type="text"].active {
  visibility: visible;
  opacity: 1;
  right: calc(100% + 8px);
}

[disabled] {
  opacity: 0.5;
  cursor: default;
  /*
	&:hover {
		background-color: $color !important;
	}
*/
}

[draggable="true"] {
  user-select: none;
  /* Required to make elements draggable in old WebKit */
  -khtml-user-drag: element;
  -webkit-user-drag: element;
}

.ui-draggable-handle {
  -ms-touch-action: none;
  touch-action: none;
}

.uploader {
  position: relative;
  z-index: 0;
}

.uploader .bar {
  position: absolute;
  z-index: 2;
  left: 0;
  top: 0;
  bottom: 0;
  width: 10px;
  background-color: black;
  pointer-events: none;
  opacity: 0;
  transition: 500ms;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.uploader.uploading .bar {
  opacity: 0.3;
}

.uploader.uploading .info {
  display: none !important;
}

.uploader img {
  max-width: 100%;
  max-height: 100%;
  z-index: 1;
}

.uploader .info {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
