/* Dropdown */
.dropdown {
  position: relative;
  cursor: auto;
}

.dropdown a,
.dropdown .dropdown-opener {
  cursor: pointer;
  margin-bottom: 0;
}

.dropdown a:not(.editing):hover + ul,
.dropdown a.open + ul,
.dropdown .dropdown-opener:not(.editing):hover + ul,
.dropdown .dropdown-opener.open + ul {
  opacity: 1;
  transition-delay: 0s !important;
  visibility: visible;
}

