}

[data-tooltip].tooltip {
  cursor: help;
}

[data-tooltip]::after {
  content: attr(data-tooltip);
  pointer-events: none;
  position: absolute;
  z-index: 999;
  display: inline-table;
  padding: 4px;
  width: 120px;
  min-height: fit-content;
  border-radius: 5px;
  background-color: black;
  color: white;
  box-shadow: 0px 0px 10px 0px rgba(255, 255, 255, 0.5);
  text-transform: initial;
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 12px;
  line-height: 15px;
  font-weight: bold !important;
  letter-spacing: normal;
  white-space: normal;
  text-align: center;
  opacity: 0;
  transition: 500ms;
  transition-delay: 0;
  bottom: 90%;
  right: auto;
  left: 50%;
  transform: translateX(-50%);
}

[data-tooltip].tooltip-active:after, [data-tooltip]:hover:after {
  opacity: 1;
  bottom: calc(100% + 5px);
  transition-delay: 500ms;
}

[data-tooltip].bottom-tooltip::after {
  bottom: auto;
  top: 90%;
}

[data-tooltip].bottom-tooltip:hover::after {
  top: calc(100% + 5px);
}

[data-tooltip].left-tooltip::after {
  right: 90%;
  left: auto;
  top: 50%;
  transform: translateY(-50%);
}

[data-tooltip].left-tooltip:hover::after {
  right: calc(100% + 5px);
}

[data-tooltip].right-tooltip::after {
  right: auto;
  left: 90%;
  top: 50%;
  transform: translateY(-50%);
}

[data-tooltip].right-tooltip:hover::after {
  left: calc(100% + 5px);
}

