  width: 100%;
  padding-right: 20px;
  box-sizing: border-box;
}

ol.categories > li.category > ol.blocks > .block .box .overlay .modes a {
  font-size: 30px;
  padding: 0 2px;
}

ol.categories > li.category > ol.blocks > .block .box .overlay .no-task {
  color: white;
  font-size: 12px;
}

ol.categories > li.category > ol.blocks > .block .box .overlay .pins {
  padding: 4px !important;
}

ol.categories > li.category > ol.blocks > .block .box .overlay .pins pin {
  margin: 1px;
  margin-bottom: 12px;
}

ol.categories > li.category > ol.blocks > .block .box .overlay .pins pin .notif-no {
  bottom: auto;
  right: -5px;
  top: -5px;
}

ol.categories > li.category > ol.blocks > .block .box .overlay .pins pin .pin-title {
  color: white;
}

ol.categories > li.category > ol.blocks > .block .box .overlay .screens > .device-wrap a > i {
  font-size: 30px;
}

ol.categories > li.category > ol.blocks > .block .box .overlay > div:not(.people):not(.pages) {
  padding: 0 2px;
}

ol.categories > li.category > ol.blocks > .block .box .overlay > div:not(.people):not(.pages) > a {
  opacity: 0.65;
}

ol.categories > li.category > ol.blocks > .block .box .overlay > div:not(.people):not(.pages) > a:hover {
  opacity: 1;
}

ol.categories > li.category > ol.blocks > .block .box:hover .overlay,
ol.categories > li.category > ol.blocks > .block .box.no-thumb .overlay {
  opacity: 1;
}

ol.categories > li.category > ol.blocks > .block .date {
  font-size: 10px;
  line-height: 22px;
  color: #4A4A4A;
  white-space: nowrap;
}

ol.categories > li.category > ol.blocks > .block .box-name a {
  font-size: 14px;
  line-height: 17px;
  letter-spacing: normal;
}

ol.categories > li.category > ol.blocks > .block input.edit-name {
  width: 90% !important;
}

ol.categories > li.category > ol.blocks .device-wrap {
  position: relative;
}

ol.categories > li.category > ol.blocks .device-wrap .device-link {
  display: inline-block;
}

ol.categories > li.category > ol.blocks .device-wrap .remove-device {
  position: absolute;
  top: -22px;
  right: -10px;
  display: none;
}

ol.categories > li.category > ol.blocks .device-wrap .remove-device > i {
  display: block;
  font-size: 15px !important;
  width: 15px;
  height: 15px;
}

ol.categories > li.category > ol.blocks .device-wrap:hover .remove-device {
