}

.cat-sortable .cat-handle {
  cursor: move;
}

.cat-sortable .sortable-placeholder {
  border: 2px dashed;
  box-sizing: border-box;
  min-height: 300px;
  margin-bottom: 20px;
  width: 100%;
}

.object-sortable .object-handle {
  cursor: move;
}

.object-sortable .sortable-placeholder {
  border: 5px dashed;
  min-height: 250px;
  min-width: 16.66667%;
  box-sizing: border-box;
}

/* Name fields to rename */
.name-field .share-icons i {
  font-size: 10px;
}

.name-field .share-icons i:nth-child(2) {
  margin-left: 1px;
}

.name-field .actions {
  opacity: 0;
  transition: 700ms;
}

.name-field .actions input.edit-name {
  display: none;
  width: auto;
  max-width: 100%;
  font-size: 14px;
  font-weight: inherit;
  line-height: inherit;
  height: auto;
  border-width: 1px;
  padding: 0 5px;
  margin-left: -5px;
}

.name-field .actions i:hover {
  color: #007acc;
}

.name-field:hover .actions {
  opacity: 1;
}

.name-field.editing .name,
.name-field.editing .fa-caret-down {
  display: none;
}

.name-field.editing .project-renamer {
  opacity: 1;
}

.name-field.editing .actions {
  opacity: 1 !important;
}

.name-field.editing .actions input.edit-name {
  display: inline-block;
  font-size: 16px;
  line-height: 18.4px;
  max-width: 75%;
}

body.dragging, body.dragging * {
  cursor: move !important;
}

.dragged {
  position: absolute;
  opacity: 0.5;
  z-index: 2000;
}

.wrap, .col:not(.dragged) {
  transition: 500ms;
}

.alerts {
  position: absolute;
  z-index: 5;
  top: 5px;
  left: 50%;
  transform: translateX(-50%);
}

.alerts.hidden {
  opacity: 0;
  pointer-events: none;
}

.alerts > .alert {
  background-color: black;
  color: white;
  font-size: 12px;
  font-weight: 100;
  padding: 10px 40px 10px 20px;
  margin-bottom: 5px;
  position: relative;
  border-radius: 3px;
}

.alerts > .alert > .close {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  color: white;
}

.alerts > .alert > .close:hover {
  opacity: 0.8;
}

.alerts > .alert.error {
  background-color: #670000;
}

.alerts > .alert.hidden {
  opacity: 0;
  pointer-events: none;
}

.revise .alerts {
  top: 60px;
}

/* Color */
.color-primary {
  color: black;
}

.color-live {
  color: #149440;
}

.color-private-live {
  color: #FC0FB3;
}

.color-private {
  color: #FC0F42;
}

/* Pricing */
table.pricing {
  width: 100%;
  margin-top: 60px;
  margin-bottom: 60px;
  border-spacing: 0;
}

.popup-window table.pricing {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 18px;
  line-height: 18px;
}

table.pricing th, table.pricing td {
  padding: 10px 5px;
}

table.pricing th.align-center, table.pricing td.align-center {
  text-align: center;
}

table.pricing th.exceed, table.pricing td.exceed {
  color: red;
}

table.pricing th.exceed span, table.pricing td.exceed span {
  color: inherit;
}

table.pricing th {
  text-transform: uppercase;
}

table.pricing th.feature {
  width: 25%;
}

table.pricing th.feature span {
  display: none;
}

table.pricing th:not(.feature) {
  width: 17%;
}

table.pricing tr:not(:last-child) td:first-child {
  border-right: 1px solid black;
}

table.pricing tr:not(:last-child) th, table.pricing tr:not(:last-child) td {
  border-bottom: 1px solid black;
}

table.pricing tr:last-child th, table.pricing tr:last-child td {
  font-weight: bold;
  position: relative;
}

table.pricing tr:last-child th a, table.pricing tr:last-child td a {
  font-weight: normal;
}

table.pricing span.current {
  color: gray;
  font-size: 10px;
}

table.pricing .cancel-plan {
  font-size: 10px;
  position: absolute;
  width: 100%;
  display: none;
}

/**
 * 14.0 - Media Queries
 */
@media only screen and (max-width: 1420px) {
  #page section:before {
    left: 40px;
  }
  #page section:after {
    right: 40px;
  }
  #page section > div:before {
    top: 40px;
  }
  #page section > div:after {
    bottom: 40px;
  }
}

@media only screen and (max-width: 1310px) {
  #page section:before {
    content: none;
  }
  #page section:after {
    content: none;
  }
  #page section > div:before {
    content: none;
  }
  #page section > div:after {
    content: none;
  }
}
